module.exports = "import * as React from \"react\";\nimport { CSSTransition, transit } from \"react-css-transition\";\n\nimport { prefix } from '../theme';\nimport Circle from \"./circle\";\n\nconst styles = prefix({\n  container: {\n    display: \"inline-block\",\n    cursor: \"pointer\",\n    borderRadius: \"10px\",\n    border: \"2px solid rgba(19, 106, 138, 0.5)\",\n    width: \"50px\",\n    padding: \"1px\",\n    overflow: \"none\",\n  },\n  circle: {\n    boxShadow: \"1px 1px 5px 0px rgba(0,0,0,0.25)\",\n    borderRadius: \"50%\",\n    background: \"#dc7d16\",\n    height: \"20px\",\n    width: \"20px\",\n  },\n  transition: {\n    style: {\n      pointerEvents: \"none\",\n    },\n    defaultStyle: {\n      transform: \"translate(0, 0)\",\n    },\n    enterStyle: {\n      transform: transit(\"translate(30px, 0)\", 200, \"ease-in-out\"),\n    },\n    leaveStyle: {\n      transform: transit(\"translate(0, 0)\", 200, \"ease-in-out\"),\n    },\n    activeStyle: {\n      transform: \"translate(30px, 0)\",\n    },\n  },\n});\n\nexport default class Toggle extends React.Component {\n  constructor(props) {\n    super(props);\n    this.state = {active: false};\n    this.handleClick = this.handleClick.bind(this);\n  }\n\n  handleClick() {\n    this.setState({active: !this.state.active});\n  }\n\n  render() {\n    return (\n      <div style={styles.container} onClick={this.handleClick}>\n        <CSSTransition {...styles.transition} active={this.state.active}>\n          <div style={styles.circle} />\n        </CSSTransition>\n      </div>\n    );\n  }\n}\n\n"


